import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map, catchError } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ContactService {
  private api = 'https://www.analysim.tech/api/communications/sendEmail'

  constructor(private http: HttpClient) { }

  sendEmail (to: any, from: any, cc: any[], bcc: any[], subject: string, bodyText: string, bodyHtml: string) : Observable<string>
  {
    let emailBody = {
        From: {
          Name: from.name,
          Address: from.address,
        },
        To: {
          Name: to.name,
          Address: to.address,
        },
        Subject: subject,
        BodyHtml: bodyHtml,
        BodyText: bodyText,
        CcAddresses: [],
        BccAddresses: []
      };

      cc.forEach(x => emailBody.CcAddresses.push({ Name: x.name, Address: x.address }));
      bcc.forEach(x => emailBody.BccAddresses.push({ Name: x.name, Address: x.address }));

      return this.http.post<any>(this.api, emailBody)
      .pipe(
        map(body => {
          console.log(body.message)
          return body.result
        }),
        catchError(error => {
          console.log(error)    
          return throwError(error)
        })
      )
  }


}
