import { Component, OnInit } from '@angular/core';
import { ContactService } from './contact.service';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'Hieu Dinh';

  constructor(private formBuilder : FormBuilder, private contact : ContactService, private toastr: ToastrService) {}

  contactForm: FormGroup;
  emailAddress : FormControl;
  subject : FormControl;
  name: FormControl;
  message: FormControl;

  ngOnInit() {
    // Set up FormControl and its Validators
    this.emailAddress = new FormControl('', [Validators.required, Validators.email])
    this.subject = new FormControl('', [Validators.required]) 
    this.name = new FormControl('', [Validators.required])
    this.message = new FormControl('',[Validators.required])

     // Initialize FormGroup using FormBuilder
    this.contactForm = this.formBuilder.group({
      'emailAddress' : this.emailAddress,
      'subject': this.subject,
      'name' : this.name,
      'message' : this.message,
    });
  }

  onSubmit() {
    let contactValue = this.contactForm.value
    this.contactForm.reset()

    const toEmail: any = { name: contactValue.name, address: contactValue.emailAddress };
    const fromEmail: any = { name: "no-reply-analysim", address: "analysim@outlook.com" };
    const subject: string = contactValue.subject;
    const bodyHtml: string = "<p>" + contactValue.message + "</p>";
    const bodyText: string = contactValue.message;
    this.contact.sendEmail(toEmail, fromEmail, [], [], subject, bodyText, bodyHtml).subscribe(
      result =>{
        this.toastr.success('Email has been sucessfully sent', 'Email Sent');
        console.log(result)
      }
    );
  } 

}
